<template>
  <div>
    <MyHeader></MyHeader>
  <div class="myappsql">
    <div class="myinputarea">
      <el-tag type="success">mybatis简单业务SQL生成</el-tag>
      <div class="myinput">
        <el-tag>请输入SQL语句尽量完整点</el-tag>
        <el-input
            type="textarea"
            :rows="15"
            placeholder="请输入SQL语句尽量完整点"
            v-model="textareainput">
        </el-input>
        <el-tag>请输入表的别名</el-tag>
        <el-input
            placeholder="请输入表的别名"
            v-model="tablename">
        </el-input>
      </div>
      <div class="mybutton">
        <el-button type="primary" round @click="getsql()">生成</el-button>
      </div>
    </div>
    <div class="myoutputarea">
      <div class="myoutput">
        <el-tag>SQL查询子句</el-tag>
        <el-input
            type="textarea"
            :rows="8"
            placeholder="SQL查询子句"
            v-model="textareaoutput.select_son_sql">
        </el-input>
        <el-tag>SQL插入</el-tag>
        <el-input
            type="textarea"
            :rows="8"
            placeholder="SQL插入"
            v-model="textareaoutput.insert_sql">
        </el-input>
        <el-tag>SQL更新</el-tag>
        <el-input
            type="textarea"
            :rows="8"
            placeholder="SQL更新"
            v-model="textareaoutput.update_sql">
        </el-input>
      </div>
    </div>
  </div>
  </div>

</template>

<script>
import MyHeader from "@/components/Common/mobile/MyHeader";
export default {
  name: "MySqlTool",
  components: {MyHeader},
  data(){
    return{
      textareainput:"",
      tablename:"",
      textareaoutput:{

      }
    }
  },
  methods:{
    getsql(){
      let this_=this
      if (this.textareainput.length>0){
        let request_={
          "SQL":this_.textareainput,
          "tabel_name":this_.tablename
        }
        this_.$http.post("http://43.143.187.107:8001/get_mybatis_sql/",request_).then((res)=>{
          this_.textareaoutput=res.data
        }).catch((err)=>{
          this_.$message.error("SQL语句语句有误！")
          console.log(err)
        })
      }
      else {
        this.$message.error("请复制SQL语句")
      }
    }
  }
}
</script>

<style scoped>
.myappsql{
  display: flex;
  flex-direction: row;
}
.myinputarea{
  display: flex;
  width: 40%;
  flex-direction: column;
  margin: 10px;
  align-items: stretch;
}
.myoutput{
  display: flex;
  width: 100%;
  flex-direction: column;
  margin: 10px;
  align-items: stretch;
}
</style>
